.sectionQuestion {
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.card,
.infoLifes {
  width: 100%;
  height: 80vh;
  background-color: rgb(97, 89, 198);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 1.5rem;
  justify-content: space-around;
  box-shadow: 0 5px 16px 0 rgb(0 0 0 / 58%);
}

.card h3,
.infoLifes h3 {
  font-family: "Ubuntu", sans-serif;
  font-size: 5vmin;
  color: white;
  text-align: center;
}
.containerLogo {
  display: flex;
  height: 30%;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.containerButtons {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.buttonOption,
.buttonOptionBombed {
  padding: 1rem 2rem;
  border: none;
  font-family: "Ubuntu", sans-serif;
  font-size: 3vmin;
  background-color: #ceb036;
  border-radius: 10px;
  color: white;
  width: 40%;
  height: 10vh;
  cursor: pointer;
  border-bottom: 5px solid #917a20;
  transition: all 0.5s;
}
.correct {
  background-color: #43b14b;
  border-bottom: 5px solid #3d8842;
}
.incorrect {
  background-color: #cb5b3b;
  border-bottom: 5px solid #943b23;
}
.buttonOptionBombed {
  background-color: #444;
  border-bottom: 5px solid rgb(51, 51, 51);
}
.logo {
  width: 13vw;
}
.buttonOption:hover {
  box-shadow: 0 5px 16px 0 rgb(0 0 0 / 58%);
}
.infoLifes {
  align-items: center;
  justify-content: center;
}
.heart {
  width: 10vw;
}

@media screen and (max-width: 600px) {
  .logo {
    width: 30vw;
  }
}
