.container {
    width: 480px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 45px 0;
}

.title {
    font-family: 'Fredoka One', cursive;
    font-size: 6vmin;
    color: #88f0c0;
    letter-spacing: 1.4px;
    font-weight: 100;
}

.container p {
    opacity: .98;
    font-family: 'Ubuntu', sans-serif;
    font-size: 3vmin;
    color: #fff;
    letter-spacing: 0;
    line-height: 24px;
    margin: 20px 20px 20px 0;
}

.buttons {
    width: 100%;
    display: flex;
    flex-direction: column;
}


.buttons p {
    width: 280px;
    letter-spacing: 1.4px;
    text-align: center;
}

.socialMedia {
    width: 40vmin;
    display: flex;
    justify-content: space-between;
}

@media (max-width: 768px) {
    .container {
        width: 90%;
        margin: 45px 0;
        align-self: center;
        text-align: center;
    }

    .buttons {
        align-items: center;
    }
}

@media (max-width: 620px) {
    .title {
        font-size: 5vmin;
    }

    .container p {
        font-size: 3.5vmin;
    }
}

@media (max-width: 500px) {

    .container p {
        font-size: 4vmin;
    }
}