html * {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.section {
    font-family: 'Fredoka One', cursive;
    width: 100%;
}
.section h2{
    color:#84eeb7;
    margin-top: 1rem;
    font-size: 34px;
    text-shadow: -1px 2px 0 #389067;
}

.section h2, .section p{
    text-align: center;
}

.section p{
    color: white;
    margin-bottom: 50px;
    font-size: 19px; 
    font-family: 'Ubuntu', sans-serif;
}

.containerCards{
    margin: 0 9%;
    display: flex;
    justify-content: center;
    display: grid;
    grid-template-columns: repeat(3, 28%);
    gap: 6vmin;
}
.card{
    background-color: #665fdf;
    font-family: 'Ubuntu', sans-serif;
    height: 480px;
    display:flex;
    flex-direction: column;
    align-items: center;
    border-radius: 3%;
    box-shadow: 0 7px 5px 0 rgb(0 0 0 / 15%);
    padding: 0.5rem;
    
}
.card h3{
    background-color: transparent;
    color: #e3be31;
    text-align: center;
    font-size: 3vmin;
    font-weight: bold;
}

.card img{
    height: 30%;
    margin: 25px 0;
}
.card img, .card p {
  background-color: transparent;
  font-family: "Ubuntu", sans-serif;
}

.card p{
    font-size: 17px;
    margin: 10px 20px;
    font-weight: 401;
}

.card p {
  font-size: 2.8vmin;
}
/* @media only screen and (max-width: 1335px) { */
@media only screen and (max-width: 1125px) {
  .containerCards {
      grid-template-columns: repeat(3, 33%);
      gap: 6vmin;
  }
}

@media only screen and (max-width: 1024px) {
  .containerCards {
      grid-template-columns: repeat(3, 37%);
      gap: 4vmin;
  }

  .card p{
    font-size: 2.6vmin;
  }
  
}

@media only screen and (max-width: 881px) {
  .containerCards {
    gap: 2vmin;
}
  .card p{
    font-size: 2.4vmin;
  }
  
}

@media only screen and (max-width: 841px) {
  .card p{
    font-size: 2.3vmin;
  }
  
}

@media only screen and (max-width: 768px) {
  .section h2 {
    font-size: 5vmin;
  }

  .containerCards {
    grid-template-columns: repeat(1, 80%);
  }
  .card p {
    font-size: 3vmin;
  }

  .card h3 {
    font-size: 4vmin;
  }
}

@media (min-width: 500px)  and (max-width: 600px) {
  .section p {
    font-size: 4vmim;
  }
}
@media only screen and (max-width: 500px) {
  .card {
    height: 480px;
  }
  .card p {
    font-size: 4vmin;
  }
}
